.wadi_video_container {
  position: relative;
}

.hide_video_thumb {
  display: none;
}

.wadi_self_hosted_video_container {
  display: none;
}
.wadi_self_hosted_video_container.show_video {
  display: block;
}

.wadi_close_sticky_container {
  display: none;
}

.video_outer_container {
  position: absolute;
  width: 100%;
  height: 100%;
  cursor: pointer;
  /* border-radius: 30px; */
}
.video_outer_container .wadi_vimeo_wrap {
  -js-display: flex;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
  margin: 10px;
  margin-right: 10px;
  -webkit-transition: opacity 0.2s ease-out;
  transition: opacity 0.2s ease-out;
  margin-right: 4.6em;
}
.video_outer_container .wadi_vimeo_wrap .wadi_vimeo_video_portrait {
  width: 60px;
  height: 60px;
  background: rgba(23, 35, 34, 0.75);
  margin-right: 1px;
  -webkit-box-flex: 1;
  -webkit-flex: 1 0 auto;
  -moz-box-flex: 1;
  -ms-flex: 1 0 auto;
  flex: 1 0 auto;
  padding: 0;
}
.video_outer_container .wadi_vimeo_wrap .wadi_vimeo_video_headers {
  font-size: 10px;
}
.video_outer_container .wadi_vimeo_wrap .wadi_vimeo_video_title {
  max-width: 100%;
  font-size: 2em !important;
  font-weight: 700;
  margin: 0;
  padding: 0.1em 0.2em;
  background: rgba(23, 35, 34, 0.75);
  display: inline-block;
  text-transform: none;
  line-height: normal;
  letter-spacing: normal;
}
.video_outer_container .wadi_vimeo_wrap .wadi_vimeo_video_byline {
  font-size: 1.2em !important;
  font-weight: 400;
  color: #fff;
  margin-top: 0.1em;
  padding: 0.2em 0.5em;
  background: rgba(23, 35, 34, 0.75);
  text-transform: none;
  line-height: normal;
  letter-spacing: normal;
}
.video_outer_container .wadi_vimeo_wrap.hide_vimeo_headers_wrap {
  display: none;
}
.video_outer_container .wadi_vimeo_video_portrait img {
  width: 50px;
  height: 50px;
  margin: 5px;
  padding: 0;
  border: 0;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}
.video_outer_container .wadi_video_thumb {
  cursor: pointer;
  height: 100%;
  position: absolute;
  width: 100%;
}
.video_outer_container .wadi_video_thumb img.wadi_video_thumbnail {
  object-fit: cover;
  height: 100%;
  width: 100%;
}
.video_outer_container .wadi_video_thumb .wadi_video_play_icon {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 50%;
  left: 50%;
  font-size: 40px;
}
.video_outer_container .wadi_video_thumb .wadi_video_text {
  position: absolute;
  display: block;
  top: 30%;
  left: 50%;
  z-index: 10;
}
.video_outer_container iframe.wadi_video_iframe {
  height: 100%;
  width: 100%;
  position: absolute;
  background: #000;
}
.video_outer_container .wadi_self_hosted_video_container.show_video {
  width: 100%;
  position: absolute;
}
.video_outer_container .wadi_video_thumb {
  cursor: pointer;
  /* padding: 20px; */
  position: relative;
  width: 100%;
}
.video_outer_container .wadi_video_thumb video.wadi_video_thumbnail {
  width: 100%;
}

.wadi_video_container .wadi_video_outer_container_sticky {
  z-index: 99;
  overflow: unset;
}
.wadi_video_container .wadi_video_outer_container_sticky .wadi_video_inner_wrapper {
  position: fixed !important;
  cursor: grab;
  height: 225px;
  width: 400px;
}
.wadi_video_container .wadi_video_outer_container_sticky .wadi_close_sticky_container {
  display: block;
  position: absolute;
  top: -10px;
  right: -5px;
  padding: 7px;
  color: #000;
  background: #fff;
  -webkit-border-radius: 100%;
  border-radius: 100%;
  cursor: pointer;
  z-index: 99999;
  height: 15px;
  width: 15px;
  line-height: 14px;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  box-shadow: inset 2px -4px 8px 1px rgba(0, 0, 0, 0.6);
  -webkit-box-shadow: inset 2px -4px 8px 1px rgba(0, 0, 0, 0.6);
  -moz-box-shadow: inset 2px -4px 8px 1px rgba(0, 0, 0, 0.6);
  transition: all 0.2s ease-in-out;
}
.wadi_video_container .wadi_video_outer_container_sticky .wadi_close_sticky_container:hover {
  transform: scale(1.3);
}
.wadi_video_container .wadi_video_wrapper .wadi_video_inner_wrapper {
  z-index: 999999999;
}

.wadi-aspect-ratio-219 .wadi_video_container .wadi_video_wrapper {
  padding-bottom: 42.8571%;
}

.wadi-aspect-ratio-169 .wadi_video_container .wadi_video_wrapper {
  padding-bottom: 56.25%;
}

.wadi-aspect-ratio-43 .wadi_video_container .wadi_video_wrapper {
  padding-bottom: 75%;
}

.wadi-aspect-ratio-32 .wadi_video_container .wadi_video_wrapper {
  padding-bottom: 66.6666%;
}

.wadi-aspect-ratio-11 .wadi_video_container .wadi_video_wrapper {
  padding-bottom: 100%;
}

.wadi-aspect-ratio-916 .wadi_video_container .wadi_video_wrapper {
  padding-bottom: 177.8%;
}

.wadi_video_sticky_top_left .wadi_video_outer_container_sticky .wadi_video_inner_wrapper {
  right: auto;
  left: 20px;
  top: 20px;
}

.wadi_video_sticky_bottom_left .wadi_video_outer_container_sticky .wadi_video_inner_wrapper {
  right: auto;
  left: 20px;
  bottom: 20px;
}

.wadi_video_sticky_top_right .wadi_video_outer_container_sticky .wadi_video_inner_wrapper {
  left: auto;
  right: 20px;
  top: 40px;
}

.wadi_video_sticky_bottom_right .wadi_video_outer_container_sticky .wadi_video_inner_wrapper {
  left: auto;
  right: 20px;
  bottom: 20px;
}

.wadi_video_sticky_center_left .wadi_video_outer_container_sticky .wadi_video_inner_wrapper {
  right: auto;
  left: 20px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.wadi_video_sticky_center_right .wadi_video_outer_container_sticky .wadi_video_inner_wrapper {
  left: auto;
  right: 20px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}